<script>
import Editor from '@/components/editor'

export default {
  name: 'single-blank-config',
  components: {
    Editor
  },
  props: {
    cType: {
      type: Number,
      require: true
    },
    configs: {
      type: Array,
      require: true
    }
  },
  watch: {
    currentValue (newValue, oldValue) {
      this.$emit('input', {
        value: this.currentValue
      })
    }
  },
  data () {
    return {
      uuid: '00000000-0000-0000-0000-0000000000018',
      currentValue: null,
      maxSizeN: null,
      maxSizeDefaultVal: null,
      valueN: null,
      valueDefaultVal: null,
      currentVisible: null,
      accepts: null,
      titles: ['全部类型', '允许上传的类型'],
      targetKeys: []
    }
  },
  created () {
    this.currentValue = this.deepCopy(this.configs)
    for (const n in this.currentValue) {
      if (this.currentValue[n].code === 'value') {
        this.valueN = n
        this.valueDefaultVal = Number(this.currentValue[n].configData.configData)
      } else if (this.currentValue[n].code === 'maxSize') {
        this.maxSizeN = n
        this.maxSizeDefaultVal = Number(this.currentValue[n].configData.configData)
      }
    }
  },
  methods: {
    numberChange (val, name) {
      this.currentValue[this[name + 'N']].configData.configData = val
    },
    renderFormat (item) {
      return item.label
    },
    handleChangeTransfer (newTargetKeys, direction, moveKeys) {
      console.log(newTargetKeys)
      console.log(direction)
      console.log(moveKeys)
      this.targetKeys = newTargetKeys
    }
  }
}
</script>

